@use "global" as *;
/* カード単体 */

.card {
  width: 100%;
  margin-right: auto;
  margin-left: auto;
}

.card__title {
  background-color: $color-main;
  color: $black;
  aspect-ratio: 1/1;
  font-size: rem(60);
  display: flex;
  align-items: center;
  justify-content: center;
  margin-right: auto;
  margin-left: auto;
  width: min(100%, rem(140));
  transition: all 1s linear 0s;
  
  @include mq() {
    width: min(100%, rem(150));
  }
}

.card__title:hover {
  @media (any-hover:hover) {
    transform: rotateY(360deg);
  }
}

.card__text {
  padding: rem(16) 0;
  text-align: justify;
}











