@use "global" as *;

table {
  table-layout: fixed;//列幅はwidthで指定した値で固定されて表示。指定がない列の列幅は、残りの幅に合わせて均等に分割
  width: 100%;
  border-collapse: collapse; //隣接するセルで境界線を共有
}

table th,td {//列の高さは指定せずにpaddingで指定した方が行が増えることを想定するといいのかも
  vertical-align: middle;
  padding: rem(10) rem(20);//高さ調整の役割
  border: 1px solid $gray-sub;
  font-size: rem(18);
  font-weight: $regular;
  letter-spacing: .1em;
  
  @include mq() {
    font-size: rem(20);
    padding: rem(16) rem(30);//高さ調整の役割
  }
}

table th {
  color: $white;
  background: $gray-sub;
  width: 55%;
  text-align: left;
  
  @include mq() {
    width: 40%;
  }
}

table td {
  background: $white;
  color: $black;
  font-family: $second-font-family;
  font-weight: $bold;
}